<template>
  <div>
    <van-button class="akm-btn" type="primary"
                @click="jump('hs')"
                size="normal">核酸检测报告</van-button>
  </div>
</template>
<script>
export default {
  name: 'Index',
  data() {
    return {
      urls: {
        hs: 'alipays://platformapi/startapp?appId=2018081661065647&page=pages%2Fservice%2Fservice%3furl%3dhttps%3A%2F%2Fakm.ahzwfw.gov.cn%2Fakm-hsjc%2Findex.html%23%2FnucleicAcidTestInfo'
      }
    }
  },
  methods: {
    jump(name) {
      let url = this.urls[name]
      if (!url) {
        return
      }
      window.ap.pushWindow(url);
    }
  }
}
</script>
<style>
.akm-btn{
  margin-top: 10px;
  margin-bottom: 10px;
}
</style>